.inputfile {

    color: rgb(142, 117, 93);
    display: inline-block;
    cursor: pointer;
    width: 0.1px;
    height: 0.1px;
    opacity: 0;
    overflow: hidden;
    position: absolute;
    z-index: -1;
}

.inputfile+label {
    padding: 0 3rem;
    font-size: 1rem;
    color: white;
    background-color: transparent;
    display: inline-block;
}

.inputfile+label {
    cursor: pointer;
    /* "hand" cursor */
}

